:root {
    scrollbar-width: none;
    -ms-overflow-style: none;
    --p_green: #4ade80;
    --p_yellow: #fcd34d;
    --p_red: #f87171;
    --dashboard_width: 20%;
    --c_dashboard : #fcfaf8;
    --c_background: #ffffff;
    --c_linebreaker: #eeeeee;
    --c_datealert: #dd726a;
    --c_icons: rgb(220, 75, 63);
    --font-family-regular: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Apple Color Emoji", Helvetica, Arial, sans-serif, "Segoe UI Emoji", "Segoe UI Symbol";
}

*:not(dialog) {
    font-family: var(--font-family-regular);  
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-size: 14px;
}

#project-modal {
    font-family: var(--font-family-regular);  
    border-radius: 10px;
    box-shadow: rgba(200, 200, 200, 0.5) 0px 0px 10px 2px, inset rgba(240, 240, 240, 0.5) 0px 0px 10px 2px;
    border: none;
    height: 15vh;
    width: 20vw;

    form {
        .dialog-legend {
            margin-bottom: 20px;
            display: flex;
            justify-content: space-between;

            p {
                font-size: 1.3rem;
                font-weight: 500;
                color: #dc4b3f;
            }

            button {
                background-color: white;
                border: none;
            }

            button:focus {
                outline: none;
            }
        }

        ul {
            padding: 10px;
            list-style: none;

            li {
                display: flex;
                align-items: center;
                gap: 10px;
                margin-bottom: 15px;

                input[type='text'] {
                    width: 80%;
                    padding: 2px 8px;
                }

                textarea:focus, input:focus{
                    outline: none;
                }

                label {
                    font-size: 1.2rem;
                }

            }

            li:not(:first-child) {
                justify-content: center;
            }
        }

        div {
            display: flex;
            justify-content: center;
            width: 100%;

            .entry {
                margin-right: 10px;
                padding: 4px 15px;
                border-radius: 8px;
                text-align: center;
                border: none;
                background-color: rgb(220, 75, 63);
                color: white;
                font-weight: 500;
            }

            .entry:hover {
                background-color: rgba(220, 75, 63, 0.8);
            }
        }
    }
}

body {
    background-image: radial-gradient(grey 4%, transparent 4%);
    background-position: 0 0;
    background-color: var(--c_background);
    background-size:  5.3vh 5.3vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    width: 100vw;
    color: #dc4b3f;

    > h1 {
        margin: 5vh auto 3.5vh;
        font-weight: 200;
        text-align: center;
        font-family: Poppins, sans-serif;
        font-size: 2.9rem;
        color: #000;
        padding: 0;
        line-height: 3.5rem !important;
        text-transform: none;
        font-weight: 600;

        span {
            font-size: 2.5rem;
            color: red;
            animation-delay: 2s;
            font-weight: 600;
            }
    }
    
    .main-content {
        width: 65vw;
        height: 70vh;
        border-radius: 15px;
        box-shadow: rgba(200, 200, 200, 0.5) 0px 0px 10px 2px, inset rgba(240, 240, 240, 0.5) 0px 0px 10px 2px;
        background-color: var(--c_background);
        display: grid;
        grid-template-columns: var(--dashboard_width) auto;

        .dashboard {
            height: 70vh;
            border-radius: 15px;
            background-color: var(--c_dashboard);

            .header {
                margin-top: 10px;
                height: 5vh;
                padding: 15px;
                display: flex;
                justify-content: space-between;
                align-items: center;

                .profile {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 0.51vw;
                    cursor: pointer;

                    button {
                        color: #dc4b3f;
                        display: flex;
                        align-items: center;
                        text-align: center;
                    }

                    img {
                        border: 2px solid #dc4b3f;
                        height: 4vh;
                        border-radius: 50%;
                    }
                }

                button {
                    border: none;
                    background-color: var(--c_dashboard);
                    cursor: pointer;
                }

            }

            > ul {
                margin-top: 5vh;
                list-style: none;
                padding-left: 0px;

                > li:first-child, > li:nth-child(2) >  div , > li:last-child {
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: start;
                    gap: 5px;
                    margin: 5px 5px;
                    border-radius: 5px;
                    color: #282828;
                    padding: 8px 5px;

                    p {
                        font-size: 1.2rem;
                    }
