                }

                li:nth-child(2) div svg:last-child {
                    position: absolute;
                    right: 10px;
                    cursor: pointer;
                }

                li:nth-child(2) > .projects-list {
                    max-height: 42vh;
                    overflow-y: hidden;
                    padding: 0.2vh 0.1vw;
                    list-style: none;


                    li {
                        display: flex;
                        justify-content: space-between;
                        padding: 5px 2vw;
                        border-radius: 8px;
                        margin-bottom: 5px;
                        color: #374151;

                        p {
