#dialog-box {
    font-family: var(--font-family-regular);  
    border-radius: 10px;
    box-shadow: rgba(200, 200, 200, 0.5) 0px 0px 10px 2px, inset rgba(240, 240, 240, 0.5) 0px 0px 10px 2px;
    border: none;

    form {
        .dialog-legend {
            margin-bottom: 20px;
            display: flex;
            justify-content: space-between;

            p {
                font-size: 1.3rem;
                font-weight: 500;
                color: #dc4b3f;
            }

            button {
                background-color: white;
                border: none;
            }
        }

        ul {
            padding: 10px;
            list-style: none;

            li {
                display: flex;
                align-items: center;
                gap: 10px;
                margin-bottom: 15px;

                input[type='text'] {
                    width: 80%;
                    padding: 2px 8px;
                }

                textarea:focus, input:focus{
                    outline: none;
                }

                label {
                    font-size: 1.2rem;
                }

            }

            li:not(:first-child) {
                justify-content: center;
            }
        }

        div {
            display: flex;
            justify-content: center;
            width: 100%;

            .entry {
                margin-right: 10px;
                padding: 4px 15px;
                border-radius: 8px;
                text-align: center;
                border: none;
                background-color: rgb(220, 75, 63);
                color: white;
                font-weight: 500;
            }

            .entry:hover {
                background-color: rgba(220, 75, 63, 0.8);
            }
        }
    }
}

.main-todos {
    border-radius: 15px;
    background-color: var(--c_background);

    > div {
        display: flex;
        align-items: center;
        margin: 6vh 3.6vw 4vh;
        gap: 10px;


        h1 {
            margin-right: auto;
            font-size: 1.75rem;
        }

        .openModel {
            display: flex;
            gap: 3px;
            padding: 4px 15px;
            border-radius: 8px;
            text-align: center;
            border: none;
            background-color: rgb(220, 75, 63);
            color: white;
            font-weight: 500;
        }

        .openModel:hover {
            background-color: rgba(220, 75, 63, 0.8);
        }

        .clear {
            display: flex;
            gap: 3px;
            padding: 2px 13px;
            border-radius: 8px;
            text-align: center;
            border: none;
            border: 2px dashed #374151;
            background-color: var(--c_background);
            color: #374151;
            font-weight: 500;
        }

        .clear:hover {
            border: 2px dashed rgba(220, 75, 63, 0.9);
            color: rgba(220, 75, 63, 0.9);
        }

        

    }


    > ul:hover,
    > ul:active,
    > ul:focus {
      overflow: hidden scroll;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: #d1d5db transparent;
      overscroll-behavior: contain;
    }

    > ul {
        max-height: 50vh;
        overflow-y: hidden;
        list-style: none;
        padding-left: 0px;


        > li:hover {
            border-bottom: 1px solid #e5e7eb;
            border-top: 1px solid #e5e7eb;

            .erase {
                color: black;
            }
        }


        > li:first-child {
            margin-top: 15px;
        }

        > li {
            width: 45vw;
            margin-left: 50px;
            border-bottom: 1px solid var(--c_background);
            border-top: 1px solid var(--c_background);
            padding: 7px 0px;
            transition: border 200ms ease-in;

            .erase {
                color: var(--c_background);
            }

            ul {
                list-style: none;
                padding-left: 0px;
                display: grid;
                grid-template-columns: 1fr 13ch 11ch 6ch 2ch;
                gap: 20px;
                justify-items: start;
                width: 100%;
                align-items: center;
                color: #374151;

                p:only-child {
                    font-size: 1.2rem;
                }


                li:has(svg + p) {
                    display: flex;
                    align-items: center;
                }

                svg + p {
                    font-size: 1rem;
                    margin-left: 3px;
                }

                svg:not(:only-child) {
                    color: #dc4b3f;
                }

                button {
                    background-color: var(--c_background);
                    border: none;

                    svg {
                        height: 100%;
                        margin-top: 3px;
                    }

                }
                
            }
        }

    }

