___CSS_LOADER_EXPORT___.push([module.id, `#note-dialog-box {
    font-family: var(--font-family-regular);  
    border-radius: 10px;
    box-shadow: rgba(200, 200, 200, 0.5) 0px 0px 10px 2px, inset rgba(240, 240, 240, 0.5) 0px 0px 10px 2px;
    border: none;

    form {
        .dialog-legend {
            margin-bottom: 20px;
            display: flex;
            justify-content: space-between;

            p {
                font-size: 1.3rem;
                font-weight: 500;
                color: #dc4b3f;
            }

            button {
                background-color: white;
                border: none;
            }
        }

        ul {
            list-style: none;

            li {
                display: flex;
                flex-direction: column;
                gap: 10px;
                margin-bottom: 15px;

                input[type='text'] {
                    padding: 2px 8px;
                    width: 100%;
                }

                textarea {
                    width: 100%;
                    resize: none;
                    overflow-y: auto;
                    padding: 5px 8px;
                }

                textarea:focus, input:focus{
                    outline: none;
                }

                label {
                    font-size: 1.2rem;
                }

            }

            li:not(:first-child) {
                justify-content: center;
            }
        }

        div {
            display: flex;
            justify-content: center;
            width: 100%;

            .entry {
                margin-right: 10px;
                padding: 4px 15px;
                border-radius: 8px;
                text-align: center;
                border: none;
                background-color: rgb(220, 75, 63);
                color: white;
                font-weight: 500;
            }

            .entry:hover {
                background-color: rgba(220, 75, 63, 0.8);
            }
        }
    }
}

.main-notes {

    .page-title {
        display: flex;
        align-items: center;
        margin: 6vh 3.6vw 4vh;
        gap: 10px;


        h1 {
            margin-right: auto;
            font-size: 1.75rem;
        }

        .openModel {
            margin-right: 10px;
            display: flex;
            gap: 3px;
            padding: 4px 15px;
            border-radius: 8px;
            text-align: center;
            border: none;
            background-color: rgb(220, 75, 63);
            color: white;
            font-weight: 500;
        }

        .openModel:hover {
            background-color: rgba(220, 75, 63, 0.8);
        }
    }

    > .grid:hover,
    > .grid:active,
    > .grid:focus {
      overflow: hidden scroll;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: #d1d5db transparent;
      overscroll-behavior: contain;
    }

    .grid {
        display: flex;
        max-height: 50vh;
        background-color: var(--c_background);
        overflow-y: hidden;
        padding: 10px 3vw;
        gap: 15px;
      
      
        .grid-col {
            display: flex;
            flex-direction: column;
            gap: 15px;
            max-width: 14.2vw;
    
            .grid-item {
                max-width: 20vw;
            
                .grid-content {
                    padding: 10px;
                    border-radius: 10px;
                    background-color: #ffffff;
                    box-shadow: inset 0px 0px 10px 2px rgba(250, 250, 250, 0.2),2px -2px 10px 2px rgba(50, 50, 50, 0.2);

                    .title {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        margin-bottom: 5px;


                        h3 {
                            font-size: 1.5rem;
                        }

                        svg {
                            color: #282828;
                        }

                    }

                    p {
                        color: #282828;
                        border: 1px solid transparent;
                        font-size: 1.1rem;
                    }
                    
                    p:focus {
                        outline: none;
                        background-color: white;
                        border: 1px solid black;
                    }
                }
            }
        }
      
    
      
    }
}`, "",{"version":3,"sources":["webpack://./src/pages/notes.css"],"names":[],"mappings":"AAAA;IACI,uCAAuC;IACvC,mBAAmB;IACnB,sGAAsG;IACtG,YAAY;;IAEZ;QACI;YACI,mBAAmB;YACnB,aAAa;YACb,8BAA8B;;YAE9B;gBACI,iBAAiB;gBACjB,gBAAgB;gBAChB,cAAc;YAClB;;YAEA;gBACI,uBAAuB;gBACvB,YAAY;YAChB;QACJ;;QAEA;YACI,gBAAgB;;YAEhB;gBACI,aAAa;gBACb,sBAAsB;gBACtB,SAAS;gBACT,mBAAmB;;gBAEnB;oBACI,gBAAgB;oBAChB,WAAW;gBACf;;gBAEA;oBACI,WAAW;oBACX,YAAY;oBACZ,gBAAgB;oBAChB,gBAAgB;gBACpB;;gBAEA;oBACI,aAAa;gBACjB;;gBAEA;oBACI,iBAAiB;gBACrB;;YAEJ;;YAEA;gBACI,uBAAuB;YAC3B;QACJ;;QAEA;YACI,aAAa;YACb,uBAAuB;YACvB,WAAW;;YAEX;gBACI,kBAAkB;gBAClB,iBAAiB;gBACjB,kBAAkB;gBAClB,kBAAkB;gBAClB,YAAY;gBACZ,kCAAkC;gBAClC,YAAY;gBACZ,gBAAgB;YACpB;;YAEA;gBACI,wCAAwC;YAC5C;QACJ;IACJ;AACJ;;AAEA;;IAEI;QACI,aAAa;QACb,mBAAmB;QACnB,qBAAqB;QACrB,SAAS;;;QAGT;YACI,kBAAkB;YAClB,kBAAkB;QACtB;;QAEA;YACI,kBAAkB;YAClB,aAAa;YACb,QAAQ;YACR,iBAAiB;YACjB,kBAAkB;YAClB,kBAAkB;YAClB,YAAY;YACZ,kCAAkC;YAClC,YAAY;YACZ,gBAAgB;QACpB;;QAEA;YACI,wCAAwC;QAC5C;IACJ;;IAEA;;;MAGE,uBAAuB;MACvB,gBAAgB;MAChB,qBAAqB;MACrB,oCAAoC;MACpC,4BAA4B;IAC9B;;IAEA;QACI,aAAa;QACb,gBAAgB;QAChB,qCAAqC;QACrC,kBAAkB;QAClB,iBAAiB;QACjB,SAAS;;;QAGT;YACI,aAAa;YACb,sBAAsB;YACtB,SAAS;YACT,iBAAiB;;YAEjB;gBACI,eAAe;;gBAEf;oBACI,aAAa;oBACb,mBAAmB;oBACnB,yBAAyB;oBACzB,mGAAmG;;oBAEnG;wBACI,aAAa;wBACb,8BAA8B;wBAC9B,mBAAmB;wBACnB,kBAAkB;;;wBAGlB;4BACI,iBAAiB;wBACrB;;wBAEA;4BACI,cAAc;wBAClB;;oBAEJ;;oBAEA;wBACI,cAAc;wBACd,6BAA6B;wBAC7B,iBAAiB;oBACrB;;oBAEA;wBACI,aAAa;wBACb,uBAAuB;wBACvB,uBAAuB;oBAC3B;gBACJ;YACJ;QACJ;;;;IAIJ;AACJ","sourcesContent":["#note-dialog-box {\n    font-family: var(--font-family-regular);  \n    border-radius: 10px;\n    box-shadow: rgba(200, 200, 200, 0.5) 0px 0px 10px 2px, inset rgba(240, 240, 240, 0.5) 0px 0px 10px 2px;\n    border: none;\n\n    form {\n        .dialog-legend {\n            margin-bottom: 20px;\n            display: flex;\n            justify-content: space-between;\n\n            p {\n                font-size: 1.3rem;\n                font